body {
  background-image: url(/img/artiom-vallat-ipjRrYEQtSQ-unsplash.jpg);
  background-position: 50% 60%;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  color: #45565d;
  height: 80vh;
  font-family: "Courier New", Courier, monospace;
  margin: 0;
}

.container {
  display: flex;
  flex-direction: column;
}

.personalinfo {
  background-color: salmon;
  text-align: center;
  padding-top: 70px;
  padding-bottom: 10px;
  margin-left: -1vw;
  margin-right: -10vw;
}
.personalinfo--guest {
  height: 100px;
}

.boxinfo {
  margin-left: 19%;
  margin-right: 25%;
}

.activities {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  text-align: center;
  margin-left: 30%;
  margin-right: 25%;
}
.activities__created {
  margin-top: 10px;
  width: 40vw;
  background-color: #f6f6f6;
  border-radius: 25px;
}
.activities__registered {
  margin-top: 10px;
  width: 40vw;
  background-color: #f6f6f6;
  border-radius: 25px;
}
.activities__buttons {
  display: flex;
  justify-content: center;
}

.me {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.btn {
  line-height: 50px;
  height: 50px;
  text-align: center;
  width: 250px;
  cursor: pointer;
  color: #FFF;
  transition: all 0.5s;
  position: relative;
}

.submit {
  z-index: 2;
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
}

.btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: all 0.5s;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background-color: rgba(255, 255, 255, 0.1);
}

.btn::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: all 0.5s;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background-color: rgba(255, 255, 255, 0.1);
}

.btn:hover::before {
  transform: rotate(-45deg);
  background-color: rgba(255, 255, 255, 0);
}

.btn:hover::after {
  transform: rotate(45deg);
  background-color: rgba(255, 255, 255, 0);
}